import axiosInstance from "../axiosInstance";

const movieInfo = {
    async getListByPage({ pageIndex, movie_name, tags }) {
        return axiosInstance.get(`/movie-info/getListByPage`, {
            params: {
                pageIndex, movie_name, tags
            }
        })
    },
    deleteById(id) {
        return axiosInstance.delete(`/movie-info/deleteById/${id}`);
    },
    //新增
    add({ movie_name, duration, movie_pc, movie_desc, publicTime, tags, role, movie_score }) {
        return axiosInstance.post(`/movie-info/add`, {
            movie_name, duration, movie_pc, movie_desc, publicTime, tags, role, movie_score
        })
    },
    findById(id) {
        return axiosInstance.get(`/movie-info/findById/${id}`);
    },
    update(id, patchData) {
        return axiosInstance.patch(`/movie-info/update/${id}`, patchData);
    },
    getAllList() {
        return axiosInstance.get(`/movie-info/getAllList`);
    }
}

export default movieInfo;