import { defineStore } from "pinia";
import Cookies from "js-cookie";


export const mainStore = defineStore("main", {
  state: () => {
    return {
      maoyanToken: "",
      userInfo: null,
      historyList: [{
        name: "Welcome",
        title: "首页"
      }],
    }
  },
  actions: {
    setMaoYanToken(maoyanToken) {
      this.maoyanToken = maoyanToken;
    },
    setUserInfo(userInfo) {
      this.userInfo = userInfo;
    },
    addToHistory(historyItem) {
      let index = this.historyList.findIndex(item => item.name === historyItem.name);
      if (index === -1) {
        this.historyList.push(historyItem);
      }
    },
    removeToHistory(routeName) {
      let index = this.historyList.findIndex(item => item.name === routeName);
      if (index !== -1) {
        this.historyList.splice(index, 1);
      }
    },
    logOut() {
      this.userInfo = null;
    }
  },
  getters: {

  },
  persist: {
    enabled: true,
    strategies: [
      {
        key: "main",
        storage: {
          getItem: (key) => Cookies.get(key),
          setItem: (key, value) => Cookies.set(key, value, { expires: 3, path: "" }),
          removeItem: (key) => Cookies.remove(key),
        },
        paths: ["maoyanToken", "userInfo"]
      }
    ]
  }
});