import axiosInstance from "../axiosInstance"

const hallInfo = {
    add({ hall_name, tags, choose_site, address }) {
        return axiosInstance.post(`/hall-info/add`, {
            hall_name, tags, choose_site, address
        })
    },
    getListByPage({ hall_name, pageIndex }) {
        return axiosInstance.get(`/hall-info/getListByPage`, {
            params: {
                hall_name,
                pageIndex
            }
        })
    },
    deleteById(id) {
        return axiosInstance.delete(`/hall-info/deleteById/${id}`);
    },
    findById(id) {
        return axiosInstance.get(`/hall-info/findById/${id}`);
    },
    update(id, patchData) {
        return axiosInstance.patch(`/hall-info/update/${id}`, patchData);
    },
    getAllList() {
        return axiosInstance.get(`/hall-info/getAllList`);
    }
}

export default hallInfo;