//路由配置文件 vue-rotuer
import { createRouter, createWebHashHistory } from "vue-router";
import Login from "../views/Login.vue";
import Register from "../views/Register.vue";
import Manager from "../views/Manager.vue";
import AddAdminInfo from "../views/adminInfo/AddAdminInfo.vue";
import AdminInfoList from "../views/adminInfo/AdminInfoList.vue";
import { mainStore } from "@/store/index.js"

//去创建一个路由管理对象
const router = createRouter({
  //路由模式
  history: createWebHashHistory(),
  // 配置路由规范
  routes: [
    {
      path: "/",
      redirect: {
        name: "Login"
      }
    },
    {
      path: "/Login",
      component: Login,
      name: "Login",
    }, {
      path: "/Register",
      component: Register,
      name: "Register",
    }, {
      path: "/Manager",
      component: Manager,
      name: "Manager",
      meta: {
        requireAuth: true
      },
      children: [
        {
          path: "Welcome",
          component: () => import("../views/Welcome.vue"),
          name: "Welcome",
          meta: {
            title: "首页",
            requireAuth: true
          }
        },
        {
          // path:"/Manager/AddAdminInfo",
          path: "AddAdminInfo",
          component: AddAdminInfo,
          name: "AddAdminInfo",
          meta: {
            title: "新增管理员",
            historyAutoClose: true,
            requireAuth: true
          }
        }, {
          path: "AdminInfoList",
          component: AdminInfoList,
          name: "AdminInfoList",
          meta: {
            title: "管理员列表",
            requireAuth: true
          }
        }
        , {
          path: "CustomInfoList",
          component: () => import("../views/customInfo/CustomInfoList.vue"),
          name: "CustomInfoList",
          meta: {
            title: "用户列表",
            requireAuth: true
          }
        }, {
          path: "MovieInfoList",
          component: () => import("../views/movieInfo/MovieInfoList.vue"),
          name: "MovieInfoList",
          meta: {
            title: "影片列表",
            requireAuth: true
          }
        }, {
          path: "AddMovieInfo",
          component: () => import("../views/movieInfo/AddMovieInfo.vue"),
          name: "AddMovieInfo",
          meta: {
            title: "新增影片",
            historyAutoClose: true,
            requireAuth: true
          }
        },
        {
          path: "EditMovieInfo/:id",
          component: () => import("../views/movieInfo/EditMovieInfo.vue"),
          name: "EditMovieInfo",
          meta: {
            title: "编辑影片",
            historyAutoClose: true,
            requireAuth: true
          }
        }, {
          path: "AddHallInfo",
          component: () => import("../views/hallInfo/AddHallInfo.vue"),
          name: "AddHallInfo",
          meta: {
            title: "新增影厅",
            requireAuth: true,
            historyAutoClose: true
          }
        }, {
          path: "HallInfoList",
          component: () => import("../views/hallInfo/HallInfoList.vue"),
          name: "HallInfoList",
          meta: {
            title: "影厅列表",
            requireAuth: true
          }
        },
        {
          path: "EditHallInfo/:id",
          component: () => import("../views/hallInfo/EditHallInfo.vue"),
          name: "EditHallInfo",
          meta: {
            title: "编辑影厅",
            historyAutoClose: true,
            requireAuth: true
          }
        },
        {
          path: "AddPlanInfo",
          component: () => import("../views/planInfo/AddPlanInfo.vue"),
          name: "AddPlanInfo",
          meta: {
            title: "电影排片",
            historyAutoClose: true,
            requireAuth: true
          }
        },
        {
          path: "PlanInfoList",
          component: () => import("../views/planInfo/PlanInfoList.vue"),
          name: "PlanInfoList",
          meta: {
            title: "排片列表",
            requireAuth: true
          }
        }
      ]
    }
  ]
});


router.beforeEach((to, from, next) => {
  let store = mainStore();
  if (to.meta.requireAuth === true) {
      if (store.maoyanToken) {
          next();
      } else {
          next({
              name: "Login"
          });
      }
  } else {
      next();
  }
});

router.afterEach((to, from) => {
  let store = mainStore();
  if (to.meta.title) {
    store.addToHistory({ name: to.name, title: to.meta.title });
  }
  if (from.meta.historyAutoClose) {
    store.removeToHistory(from.name);
  }
})


export default router;