<template>
  <div class="login-page">
    <img src="../assets/img/bg.jpg" class="bg-img" alt="">
    <transition enter-active-class="animate__animated animate__flipInY">
      <div class="login-box" v-show="isShow">
        <h2 class="box-title">
          <el-icon size="36px" style="margin-right: 5px;">
            <Platform />
          </el-icon>
          华夏影院管理平台
        </h2>
        <!-- 登录表单 -->
        <el-form ref="loginFormEl" :rules="formDataRules" :model="formData" class="login-form">
          <el-form-item prop="account">
            <el-input v-model="formData.account" placeholder="请输入手机号"></el-input>
          </el-form-item>
          <el-form-item prop="pwd">
            <el-input v-model="formData.pwd" show-password placeholder="请输入密码"></el-input>
          </el-form-item>
          <el-form-item>
            <el-switch v-model="formData.saveAccount" active-text="记住账号"></el-switch>
          </el-form-item>
          <el-form-item>
            <el-button @click="submitForm" class="btn-login" type="primary" :loading="isSubmiting">登录系统</el-button>
          </el-form-item>
        </el-form>
      </div>
    </transition>
  </div>
</template>

<script setup>
import { Platform } from "@element-plus/icons-vue";
import { ElMessage, ElMessageBox } from "element-plus";
import { ref, reactive, onMounted } from "vue";
import API from "@/utils/API";
import WebStorageCache from "web-storage-cache";
import { mainStore } from "@/store/index.js";
import { useRouter } from "vue-router";

//获取pinia的store
const store = mainStore();

const router = useRouter();
const wsCache = new WebStorageCache({
  storage: "localStorage",
});
const formData = reactive({
  account: "",
  pwd: "",
  saveAccount: false,
});

const formDataRules = {
  account: [{ required: true, message: "请输入手机号", trigger: "blur" }],
  pwd: [{ required: true, message: "请输入密码", trigger: "blur" }],
};

const isShow = ref(false);
const loginFormEl = ref(null);
const submitForm = () => {
  loginFormEl.value.validate((valid) => {
    if (valid) {
      checkLogin();
    } else {
      ElMessage.error("请输入手机和密码进行登录");
    }
  });
};

const isSubmiting = ref(false);
const checkLogin = () => {
  isSubmiting.value = true;
  API.adminInfo
    .checkLogin({
      admin_tel: formData.account,
      admin_pwd: formData.pwd,
    })
    .then((result) => {
      if (formData.saveAccount) {
        wsCache.add("admin_tel", formData.account);
      }
      console.log(result.data);
      store.setMaoYanToken(result.data.token);
      store.setUserInfo(result.data.tokenUserInfo);
      router.replace({ name: "Welcome" });
    })
    .catch((error) => {
      ElMessageBox.alert(error.msg, "错误", {
        confirmButtonText: "确定",
      });
    })
    .finally(() => {
      isSubmiting.value = false;
    });
};

onMounted(() => {
  isShow.value = true;
  if (wsCache.get("admin_tel")) {
    formData.account = wsCache.get("admin_tel");
    formData.saveAccount = true;
  }
});
</script>

<style scoped lang="scss">
.login-page {
  width: 100%;
  height: 100%;
  position: relative;

  .bg-img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }

  .login-box {
    width: 400px;
    box-shadow: 0px 0px 15px 5px lightgray;
    position: absolute;
    left: calc(50% - 200px);
    top: 150px;
    background-color: #fff;
    box-sizing: border-box;
    padding: 10px;

    .box-title {
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 26px;
      color: #333;
      border-bottom: 1px dashed black;
      padding: 10px 0;
    }

    .login-form {
      margin-top: 15px;

      .btn-login {
        width: 100%;
      }
    }
  }
}
</style>