<template>
  <el-card>
    <template #header>
      <div class="header-box">
        <span>添加管理员</span>
        <!-- <span @click="$router.push({name:'AdminInfoList'})">返回列表</span> -->
        <router-link :to="{name:'AdminInfoList'}">返回列表</router-link>
      </div>
    </template>
    <!-- 这里需要一个表单 -->
    <el-form
      label-width="120px"
      :model="formData"
      :rules="formDataRules"
      ref="adminInfoForm">
      <el-form-item label="管理员名称" prop="admin_name">
        <el-input placeholder="请输入管理员名称" v-model="formData.admin_name" />
      </el-form-item>
      <el-form-item label="管理员手机号" prop="admin_tel">
        <el-input placeholder="请输入管理员手机号" v-model="formData.admin_tel" />
      </el-form-item>
      <el-form-item label="管理员密码" prop="admin_pwd">
        <el-input show-password placeholder="请输入密码" v-model="formData.admin_pwd" />
      </el-form-item>
      <el-form-item label="确认密码" prop="confirm_pwd">
        <el-input show-password placeholder="请再次输入密码，两次密码必须相同" v-model="formData.confirm_pwd" />
      </el-form-item>
      <el-form-item label="管理员性别">
        <!-- 单选框 -->
        <el-radio-group v-model="formData.admin_sex">
          <el-radio label="男">男</el-radio>
          <el-radio label="女">女</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="管理员邮箱" prop="admin_email">
        <el-input placeholder="请输入管理员的邮箱" v-model="formData.admin_email"/>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm">保存数据</el-button>
        <el-button type="danger" @click="resetForm">重置表单</el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script>
import API from "../../utils/API/index.js";
import {ElMessage} from "element-plus";
export default {
  name: "AddAdminInfo",
  data() {
    return {
      formData: {
        admin_name: "",
        admin_tel: "",
        admin_pwd: "",
        admin_sex: "男",
        admin_email: "",
        confirm_pwd: "",
      },
      //配置验证规则
      formDataRules: {
        admin_name: [
          { required: true, message: "管理员名称不能为空", trigger: "blur" },
        ],
        admin_tel: [
          { required: true, message: "管理员手机号不能为空", trigger: "blur" },
          {
            validator(rule, value, callBack) {
              //要通过正则表达式来验证手机号
              let reg = /^1[356789]\d{9}$/;
              if (reg.test(value)) {
                //验证通过
                callBack();
              } else {
                //验证不能通过
                callBack(new Error("手机号格式不正确"));
              }
            },
            trigger: "blur",
          },
        ],
        admin_pwd: [
          { required: true, message: "密码不能为空", trigger: "blur" },
        ],
        confirm_pwd: [
          { required: true, message: "确认密码不能为空", trigger: "blur" },
          {
            validator: (rule, value, callBack) => {
              if (value == this.formData.admin_pwd) {
                callBack();
              } else {
                callBack(new Error("两次密码不一致"));
              }
            },
            trigger: "blur",
          },
        ],
        admin_email:[
          { required: true, message: "邮箱不能为空", trigger: "blur" },
          { 
            pattern:/\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/,
            message:"邮箱格式不正确",
            trigger:"blur"
          }
        ]
      },
    };
  },
  methods:{
    //提交表单
    submitForm() {
      this.$refs["adminInfoForm"].validate(async valid=>{
        if(valid){
          //表单验证通过了,调用接口，将数据提交到后台
          //API.adminInfo.add(this.formData).then(resp=>{}).catch(error=>{});
            let result = await API.adminInfo.add(this.formData);
            //提交成功
            //resp代表服务器返回的内容
            //console.log("成功",result);
            //弹出消息，提示“新增成功”,然后跳转到AdminInfoList的页面
            ElMessage.success("新增成功");
            this.$router.replace({name:"AdminInfoList"});
        }
      });
    },
    //重置表单
    resetForm(){
      this.$refs["adminInfoForm"].resetFields();
    }
  },
};
</script>
<style scoped lang="scss">
.header-box {
  display: flex;
  justify-content: space-between;
}
</style>