<template>
  <el-container class="manager-page">
    <el-aside width="200px" class="left-box">
      <LeftMenu />
    </el-aside>
    <el-container>
      <el-header height="50px" class="top-box">
        <el-icon>
          <Fold />
        </el-icon>
        <div class="history-list">
          <router-link
            :to="{name:item.name}"
            custom
            v-for="item in historyList"
            #default="{navigate,isActive}"
            :key="item.name">
            <div class="history-item" :class="{'active':isActive}" @click="navigate">
              {{ item.title }}
              <span class="close" @click.stop="removeToHistory(item.name)">x</span>
            </div>
          </router-link>
        </div>
        <!-- 下拉菜单 -->
        <el-dropdown class="right-user-info cursor-pointer" @command="dropdownCommand">
          <span>
            欢迎登录：{{userInfo?.admin_name}}
            <el-icon>
              <ArrowDown />
            </el-icon>
          </span>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item>我的信息</el-dropdown-item>
              <el-dropdown-item>修改密码</el-dropdown-item>
              <el-dropdown-item divided :command="{type:'logOut'}">退出系统</el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </el-header>
      <el-main class="main-box">
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>


<script setup>
import LeftMenu from "../components/LeftMenu.vue";
import { Fold, ArrowDown } from "@element-plus/icons-vue";
import { mainStore } from "@/store/index.js";
import { storeToRefs } from "pinia";
import { useRouter } from "vue-router";
import { ElMessageBox, MessageBox } from "element-plus";

const store = mainStore();
const { historyList, userInfo } = storeToRefs(store);
const router = useRouter();
const dropdownCommand = (command) => {
  const { type } = command;
  if (type == "logOut") {
    ElMessageBox.confirm("确定要退出吗", "询问", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning",
    }).then(() => {
      store.logOut();
      router.replace({
        name: "Login",
      });
    });
  }
};

const removeToHistory = (routeName) => {
  store.removeToHistory(routeName);
};
</script>

<style lang="scss" scoped>
.manager-page {
  height: 100%;

  .left-box {
    border-right: 1px solid #ececec;
    overflow: hidden;
  }

  .top-box {
    border-bottom: 1px solid lightgray;
    padding: 0px 5px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .right-user-info {
      margin-right: 20px;
      color: deeppink;
    }
  }

  .main-box {
    background-color: #f5f7f9;
    padding: 5px;
  }
}

.history-list {
  scroll-snap-type: x mandatory;
  @apply flex-1 overflow-auto flex flex-row h-full items-center px-[10px];
}

.history-list::-webkit-scrollbar {
  height: 5px;
  background-color: rgba($color: #000000, $alpha: 0.1);
}

.history-list::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.history-item {
  @apply border border-solid border-gray-400 text-[12px] px-[3px] min-w-[50px] h-[26px] flex flex-row justify-center items-center mr-[5px] cursor-pointer relative;
  transition: all 0.3s linear;
  flex-shrink: 0;
  scroll-snap-align: start;

  &:hover {
    @apply border-blue-400 text-blue-400;
    & > .close {
      @apply scale-95;
    }
  }

  > .close {
    transition: all;
    transition-duration: 0.3s;
    @apply bg-white absolute transform scale-0 right-[-5px] top-[-5px] border border-solid border-gray-400 rounded w-[10px] h-[10px] flex flex-row items-center justify-center;
    &:hover {
      @apply text-red-600 border-red-600;
    }
  }

  &.active {
    background-color: #409eff;
    color: white;

    &:hover {
      .el-icon {
        border-color: orangered;
        color: orangered;
      }
    }
  }
}
</style>