import axiosInstance from "../axiosInstance";

const planInfo = {
    add({ plan_time, hid, mid, price, choose_site }) {
        return axiosInstance.post(`/plan-info/add`, {
            plan_time, hid, mid, price, choose_site
        })
    },
    update(id, patchData) {
        return axiosInstance.patch(`/plan-info/update/${id}`, patchData)
    },
    deleteById(id) {
        return axiosInstance.delete(`/plan-info/delete/${id}`)
    },
    findById(id) {
        return axiosInstance.get(`/plan-info/find/${id}`)
    },
    getListByPage({ pageIndex, hid, mid, start_time, end_time }) {
        return axiosInstance.get(`/plan-info/getListByPage`, {
            params: {
                pageIndex, hid, mid, start_time, end_time
            }
        });
    }
}

export default planInfo;