/**
 * 对应后端接口里面的管理员模块admin_info
 */
import axiosInstance from "../axiosInstance";

const adminInfo = {
    //新增管理员信息
    add({ admin_name, admin_pwd, admin_tel, admin_email, admin_sex }) {
        return axiosInstance.post("/admin-info/add", {
            admin_name, admin_pwd, admin_tel, admin_email, admin_sex
        });
    },
    //分页查询管理员信息
    getListByPage({ pageIndex, admin_name, admin_tel, admin_email }) {
        return axiosInstance.get("/admin-info/getListByPage", {
            params: {
                pageIndex, admin_name, admin_tel, admin_email
            }
        })
    },
    //根据ID删除管理员信息
    deleteById(id) {
        //http://www.softeem.xin:9999/admin-info/deleteById/{id}
        return axiosInstance.delete("/admin-info/deleteById/" + id);
    },
    checkLogin({ admin_tel, admin_pwd }) {
        return axiosInstance.post("/admin-info/checkLogin", {
            admin_tel, admin_pwd
        });
    }
}

export default adminInfo;