/**
 * 专门用于配置axios的实例
 */
import axios from 'axios';
import { ElNotification } from "element-plus";
import router from "@/router";
import { mainStore } from '@/store/index.js';

//得到axios的实例
const axiosInstance = axios.create({
    baseURL: process.env.VUE_APP_APIURL,
    timeout: 10000,
});


//后期我们还可以在这里配置请求拦截器 和  响应拦截器


axiosInstance.interceptors.request.use(requestConfig => {
    //在请求发送之前做一些处理
    let store = mainStore();
    requestConfig.headers.maoyan_token = store.maoyanToken || "";
    return requestConfig;
})

//配置响应拦截器，如果请求成功了，我们就直接提取resp.data,如果请求失败了，我们就弹出一个
//对话框告诉用户

axiosInstance.interceptors.response.use(resp => {
    if (resp.status === 403) {
        router.replace({
            name: "Login"
        })
    }
    else {
        if (resp.data.status == "success") {
            return Promise.resolve(resp.data);   //把这个结果直接放在try里面
        }
        else {
            ElNotification.error({
                title: '错误',
                message: resp.data.msg,
            });
            return Promise.reject(resp.data);
        }
    }

}, error => {
    if (error.response) {
        if (error.response.status === 403) {
            router.replace({
                name: "Login"
            }).then(() => {
                ElMessageBox.alert("登陆信息已过期，请重新登陆", "提示");
            })
        }
        else {
            ElNotification.error({
                title: "请求失败",
                message: "服务器请求失败，请重试或联系管理员"
            })
        }
    }
    return Promise.reject(error);
});

export default axiosInstance;