import axiosInstance from "../axiosInstance";

/**
 * 对接后端接口里的custom_info
 */
const customInfo = {
    getListByPage({ pageIndex, custom_name, custom_sex, custom_tel }) {
        return axiosInstance.get(`/custom-info/getListByPage`, {
            params: {
                pageIndex, custom_name, custom_sex, custom_tel
            }
        })
    },
    deleteById(id){
        return axiosInstance.delete(`/custom-info/deleteById/${id}`);
    }
}

export default customInfo;