<template>
  <el-card header="管理员列表">
    <el-form :inline="true">
      <el-form-item label="管理员名称">
        <el-input placeholder="管理员名称查询" v-model="queryForm.admin_name" />
      </el-form-item>
      <el-form-item label="手机号">
        <el-input placeholder="管理员手机号查询" v-model="queryForm.admin_tel" />
      </el-form-item>
      <el-form-item label="邮箱">
        <el-input placeholder="管理员邮箱查询" v-model="queryForm.admin_email" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" :icon="Search" :loading="isLoading" @click="queryForm.pageIndex=1;queryData()">查询</el-button>
        <el-button type="default" :icon="CirclePlus">新增管理员</el-button>
      </el-form-item>
    </el-form>
    <!-- 给一个表格 -->
    <el-table :data="listData" stripe border v-loading="isLoading" element-loading-text="正在加载数据">
      <el-table-column label="ID" prop="id" width="60" align="center"></el-table-column>
      <el-table-column label="管理员名称" prop="admin_name" width="150"></el-table-column>
      <el-table-column label="手机号" prop="admin_tel"></el-table-column>
      <el-table-column label="邮箱" prop="admin_email"></el-table-column>
      <el-table-column label="性别" width="100" align="center">
        <template #default="{row}">
          <el-tag v-if="row.admin_sex=='男'">男</el-tag>
          <el-tag v-else-if="row.admin_sex=='女'" type="danger">女</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="密码">
        <template #default="{row}">
          {{new Array(row.admin_pwd.length).fill("*").join("")}}
        </template>
      </el-table-column>
      <!-- 如果在表单里某一列想显示自定义内容，可以使用下面的方法 -->
      <el-table-column label="操作" width="150" align="center">
        <template #default="{row}">
          <el-button type="warning" size="small">编辑</el-button>
          <el-popconfirm title="你确定要删除吗?" @confirm="deleteCurrentRow(row)">
            <template #reference>
              <el-button type="danger" size="small">删除</el-button>
            </template>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div class=" flex flex-row justify-between items-center mt-[10px]">
      <div>当前第{{queryForm.pageIndex}}页，共{{pageCount}}页，共{{totalCount}}条</div>
      <el-pagination
        background
        layout="prev,pager,next"
        :total="totalCount"
        @current-change="currentPageChange"
        :current-page="queryForm.pageIndex"></el-pagination>
    </div>
  </el-card>
</template>
<script>
import { Search, CirclePlus } from "@element-plus/icons-vue";
import {ElMessage,ElMessageBox} from "element-plus";
// 导入模拟数据
import listData from "./listData.js";
import API from "../../utils/API/index.js";
export default {
  name: "AdminInfoList",
  data() {
    return {
      queryForm: {
        admin_name: "",
        admin_tel: "",
        admin_email: "",
        pageIndex: 1,
      },
      Search,
      CirclePlus,
      listData: [],
      // 共多少页
      pageCount: 0,
      // 共多少条
      totalCount: 0,
      //代表当前是否正在加载数据
      isLoading:true
    };
  },
  methods: {
    async queryData() {
      this.isLoading = true;
      let results = await API.adminInfo.getListByPage(this.queryForm);
      this.pageCount = results.data.pageCount;
      this.totalCount = results.data.totalCount;
      this.queryForm.pageIndex = Number(results.data.pageIndex);
      this.listData = results.data.listData;
      this.isLoading = false;
    },
    //当页面发生变化的时候,参数page就是新的页码的值
    currentPageChange(page) {
      this.queryForm.pageIndex = page;
      this.queryData();
    },
    //删除当前行的操作
    async deleteCurrentRow(row){
      let results = await API.adminInfo.deleteById(row.id);
      if(results.status=="success"){
        //删除成功
        ElMessage.success("删除成功");
        this.queryData();   //调用方法，重新获取数据
      }
      else{
        //删除失败
        ElMessageBox.alert("删除失败，请重试或联系管理员","提示");
      }
    }
  },
  //当前页面打开的时候,会自动执行一次
  created() {
    this.queryData();
  },
};
</script>
<style scoped>
</style>